<template>
  <div id="shopnoticeInfo">
    <el-button class="btn" @click="back()" type="primary">返回</el-button>
    <div>
        <div class="title">
            <h2 style="font-weight:400">【精准防违规验号】收费调整：自2021年8月31日18点起每单收费0.5元，收取的佣金会发放给买手，以提高买手验号积极性</h2>
            <h3 style="font-weight:200" class="marginY-10">发布时间：2021-08-31 15:08:27</h3>
        </div>
        <div class="content">
            <p>【精准防违规验号】补单数据总结：使用精准防违规验号的链接，降权率远远低于创客等第三方验号接口。对于每日补单量较大的商家，可选择部分链接发布创客验号，切记单个链接不要多种验号方式混刷，要保证每个链接验号方式的单一性。

收费调整：由于新型验号方式对买号要求较高，以及高验号频率，导致目前部分活跃买手流失。平台自2021年8月31日18点起将对【精准防违规验号】每单收费0.5元，收取的佣金会发放给买手，以提高买手验号积极性。</p>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "shopnoticeInfo",
  data() {
    return {};
  },
  methods:{
      back(){
          this.$router.go(-1)
        //   window.history.go(-1);
      }
  }
};
</script>

<style scoped>
#shopnoticeInfo {
  /* height: 100%; */
  background: #fff;
  padding: 20px;
  margin-bottom: 20px;
  overflow-x: none;
}
.btn {
  padding: 5px 10px;
  border-radius: 5px;
  margin: 10px;
  transition: all 0.5s;
  background: #3672e8;
  border: none;
  font-size: 18px;
}
.btn:hover {
  background: #5ae1f7;
  transition: all 0.5s;
}
</style>



