<template>
  <div id="publishtask">
    <div class="top-box tt-column">
      <div class="tt-row buzhou marginY-20">
        <h2>发布任务流程:</h2>
        <div class="step" :class="{ step1: $store.state.step == 1 }">
          <p>1.选择任务类型</p>
        </div>
        <div class="point">
          <div
            class="point1"
            :class="{ point1_1: $store.state.step == 1 }"
          ></div>
          <div
            class="point2"
            :class="{ point2_1: $store.state.step == 1 }"
          ></div>
          <div
            class="point3"
            :class="{ point3_1: $store.state.step == 1 }"
          ></div>
        </div>
        <div class="step" :class="{ step1: $store.state.step == 2 }">
          <p>2.填写任务信息</p>
        </div>
        <div class="point">
          <div
            class="point1"
            :class="{ point1_1: $store.state.step == 2 }"
          ></div>
          <div
            class="point2"
            :class="{ point2_1: $store.state.step == 2 }"
          ></div>
          <div
            class="point3"
            :class="{ point3_1: $store.state.step == 2 }"
          ></div>
        </div>
        <div class="step" :class="{ step1: $store.state.step == 3 }">
          <p>3.支付</p>
        </div>
        <div class="point">
          <div
            class="point1"
            :class="{ point1_1: $store.state.step == 3 }"
          ></div>
          <div
            class="point2"
            :class="{ point2_1: $store.state.step == 3 }"
          ></div>
          <div
            class="point3"
            :class="{ point3_1: $store.state.step == 3 }"
          ></div>
        </div>
        <div class="step" :class="{ step1: $store.state.step == 4 }">
          <p>4.发布成功</p>
        </div>
        <div class="point">
          <div
            class="point1"
            :class="{ point1_1: $store.state.step == 4 }"
          ></div>
          <div
            class="point2"
            :class="{ point2_1: $store.state.step == 4 }"
          ></div>
          <div
            class="point3"
            :class="{ point3_1: $store.state.step == 4 }"
          ></div>
        </div>
      </div>
      <div>
        <h2 style="font-size: 18px; color: #ef3314;margin">
          关于平台对买手账号筛选的标准
        </h2>
        <p class="p1">
          1.买手一部手机只能注册一个账户，一个账户只能绑定一个淘宝账号
        </p>
        <p class="p1">
          2.日不超过2单，周不超过7单。这是我们为商家考量最基本的要求。
        </p>
        <p class="p1">
          3.近一个月内被删除评价的账号则无法接单，近3个月内被删除评价的账号无法接优质任务单
        </p>
      </div>
    </div>
    <div class="task">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
export default {
  name: "publishtask",
  metaInfo: {
    title: "淘小富-发布任务",
    meta: [
      { charset: "utf-8" },
      {
        name: "viewport",
        content:
          "width=device-width,initial-scale=1,minimum-scale=1,maximum-scale=1,user-scalable=no",
      },
    ],
  },
  data() {
    return {
      radio: null,
    };
  },
};
</script>

<style scoped>
.top-box {
  width: 100%;
  height: 230px;
  background: #fff;
  padding: 20px;
}
.top-box h2 {
  font-size: 20px;
  color: #0098e1;
  margin-right: 50px;
}
.step {
  border: 10px solid #f0f1f5;
  border-left: none;
  width: 175px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #5f6373;
  font-size: 18px;
  font-weight: bold;
}
.step1 {
  border: 10px solid #445bc2;
  border-left: none;
}
.point {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 66px;
  padding: 0 50px;
}
.point1 {
  border-radius: 50%;
  width: 12px;
  height: 12px;
  background: #fff;
}
.point1_1 {
  background: #ebeef9;
}
.point2 {
  border-radius: 50%;
  width: 14px;
  height: 14px;
  background: #ebeef9;
}
.point2_1 {
  background: #c8cded;
}
.point3 {
  border-radius: 50%;
  width: 16px;
  height: 16px;
  background: #c8cded;
}
.point3_1 {
  background: #697cce;
}
.p1 {
  font-size: 14px;
  margin: 10px 0 0;
  color: #646876;
  font-weight: bold;
}
.task {
  margin-top: 20px;
  width: 100%;
  padding: 20px;
  background: #fff;
}
</style>



