import Vue from 'vue'
import Router from 'vue-router'
import Menu from '@/components/menu'
import Login from '@/components/login'
import Sign from '@/components/sign'
import shopnotice from '@/components/shopnotice'
import shopnoticeInfo from '@/components/shopnotice-info'
import publishtask from '@/components/publishtask'
import publishtask1 from '@/components/publishtask1'
import publishtask2 from '@/components/publishtask2'
import publishtask3 from '@/components/publishtask3'
import publishtask4 from '@/components/publishtask4'
import batchPublishTask from '@/components/batchPublishTask'
import disbursementTask from '@/components/disbursementTask'
import browseTask from '@/components/browseTask'
import evaluateTask from '@/components/evaluateTask'
import accountaudit from '@/components/accountaudit'
import shopmanage from '@/components/shopmanage'
import evaluateManage from '@/components/evaluateManage'
import revocationorder from '@/components/revocationorder'
import yetrevocationorder from '@/components/yetrevocationorder'
import withdrawPay from '@/components/withdrawPay'
import funddetail from '@/components/funddetail'
import appealcenter from '@/components/appealcenter'
import pricedetail from '@/components/pricedetail'
import invitefriend from '@/components/invitefriend'
import blacklist from '@/components/blacklist'

Vue.use(Router)

export default new Router({
  routes: [ 
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/sign',
      name: 'Sign',
      component: Sign
    },
    {
      path: '/',
      name: 'Menu',
      component: Menu,
      children:[
        {
          path: '/',
          component: shopnotice
        },
        {
          path: 'shopnoticeInfo',
          component: shopnoticeInfo
        },
        {
          path: 'publishtask',
          component: publishtask,
          children: [
            {
              path: '/',
              component: publishtask1
            },
            {
              path: 'publishtask2',
              component: publishtask2
            },
            {
              path: 'publishtask3',
              component: publishtask3
            },
            {
              path: 'publishtask4',
              component: publishtask4
            },
          ]
        },
        
        {
          path: 'batchPublishTask',
          component:batchPublishTask 
        },
        {
          path: 'disbursementTask',
          component: disbursementTask
        },
        {
          path: 'browseTask',
          component: browseTask
        },
        {
          path: 'evaluateTask',
          component: evaluateTask
        },
        {
          path: 'accountaudit',
          component: accountaudit
        },
        {
          path: 'shopmanage',
          component: shopmanage
        },
        {
          path: 'evaluateManage',
          component: evaluateManage
        },
        {
          path: 'revocationorder',
          component: revocationorder
        },
        {
          path: 'yetrevocationorder',
          component: yetrevocationorder
        },
        {
          path: 'withdrawPay',
          component: withdrawPay
        },
        {
          path: 'funddetail',
          component: funddetail
        },
        {
          path: 'appealcenter',
          component: appealcenter
        },
        {
          path: 'pricedetail',
          component: pricedetail
        },
        {
          path: 'invitefriend',
          component: invitefriend
        },
        {
          path: 'blacklist',
          component: blacklist
        }
      ]
    },
  ]
})



