<template>
  <div id="menu" class="tt-row">
    <div class="left">
      <div class="logo align-center">
        <img src="../assets/login/logo.png" alt="" />
      </div>
      <ul>
        <router-link to="/">
          <li
            class="tt-row"
            :class="{ li: viewid == 1 }"
            @click="cview(1, '商家公告')"
          >
            <i class="iconfont icon-dianpu-copy"></i>
            <p>商家公告</p>
          </li>
        </router-link>
        <router-link to="/publishtask">
          <li
            class="tt-row"
            :class="{ li: viewid == 2 }"
            @click="cview(2, '发布任务')"
          >
            <i class="iconfont icon-renwu-tianchong"></i>
            <p>发布任务</p>
          </li>
        </router-link>
        <router-link to="/batchPublishTask">
          <li
            class="tt-row"
            :class="{ li: viewid == 3 }"
            @click="cview(3, '批量任务导入')"
          >
            <i class="iconfont icon-menu_fbgl"></i>
            <p>批量任务导入</p>
          </li>
        </router-link>
        <router-link to="/disbursementTask">
          <li
            class="tt-row"
            :class="{ li: viewid == 4 }"
            @click="cview(4, '垫付任务管理')"
          >
            <i class="iconfont icon-fukuanfangshisel"></i>
            <p>垫付任务管理</p>
          </li>
        </router-link>
        <router-link to="/browseTask">
          <li
            class="tt-row"
            :class="{ li: viewid == 5 }"
            @click="cview(5, '浏览任务管理')"
          >
            <i class="iconfont icon-liulan-2"></i>
            <p>浏览任务管理</p>
          </li>
        </router-link>
        <router-link to="/evaluateTask">
          <li
            class="tt-row"
            :class="{ li: viewid == 6 }"
            @click="cview(6, '逛逛/追评管理')"
          >
            <i class="iconfont icon-xiaoxi"></i>
            <p>逛逛/追评管理</p>
          </li>
        </router-link>
        <router-link to="/accountaudit">
          <li
            class="tt-row"
            :class="{ li: viewid == 7 }"
            @click="cview(7, '待审核买号管理')"
          >
            <i class="iconfont icon-shenhe"></i>
            <p>待审核买号管理</p>
          </li>
        </router-link>
        <router-link to="/shopmanage">
          <li
            class="tt-row"
            :class="{ li: viewid == 8 }"
            @click="cview(8, '店铺/抖音号管理')"
          >
            <i class="iconfont icon-dianpu-copy"></i>
            <p>店铺/抖音号管理</p>
          </li>
        </router-link>
        <router-link to="/evaluateManage">
          <li
            class="tt-row"
            :class="{ li: viewid == 9 }"
            @click="cview(9, '评价管理')"
          >
            <i class="iconfont icon-pingjia"></i>
            <p>评价管理</p>
          </li>
        </router-link>
        <router-link to="/revocationorder">
          <li
            class="tt-row"
            :class="{ li: viewid == 10 }"
            @click="cview(10, '可撤销订单')"
          >
            <i class="iconfont icon-chexiaoshenqingdanguanli"></i>
            <p>可撤销订单</p>
          </li>
        </router-link>
        <router-link to="/yetrevocationorder">
          <li
            class="tt-row"
            :class="{ li: viewid == 11 }"
            @click="cview(11, '已撤销订单')"
          >
            <i class="iconfont icon-yewushenqingchexiao"></i>
            <p>已撤销订单</p>
          </li>
        </router-link>
        <router-link to="/withdrawPay">
          <li
            class="tt-row"
            :class="{ li: viewid == 12 }"
            @click="cview(12, '充值提现')"
          >
            <i class="iconfont icon-chongzhi2"></i>
            <p>充值提现</p>
          </li>
        </router-link>
        <router-link to="/funddetail">
          <li
            class="tt-row"
            :class="{ li: viewid == 13 }"
            @click="cview(13, '资金明细')"
          >
            <i class="iconfont icon-qianbao"></i>
            <p>资金明细</p>
          </li>
        </router-link>
        <router-link to="/appealcenter">
          <li
            class="tt-row"
            :class="{ li: viewid == 14 }"
            @click="cview(14, '申诉中心')"
          >
            <i class="iconfont icon-yichangshensu"></i>
            <p>申诉中心</p>
          </li>
        </router-link>
        <router-link to="/pricedetail">
          <li
            class="tt-row"
            :class="{ li: viewid == 15 }"
            @click="cview(15, '价格明细')"
          >
            <i class="iconfont icon-renminbicaiwuzhifu"></i>
            <p>价格明细</p>
          </li>
        </router-link>
        <router-link to="/invitefriend">
          <li
            class="tt-row"
            :class="{ li: viewid == 16 }"
            @click="cview(16, '邀请好友')"
          >
            <i class="iconfont icon-yaoqinghaoyou"></i>
            <p>邀请好友</p>
          </li>
        </router-link>
        <router-link to="/blacklist">
          <li
            class="tt-row"
            :class="{ li: viewid == 17 }"
            @click="cview(17, '黑名单')"
          >
            <i class="iconfont icon-4fayuanheimingdan272636"></i>
            <p>黑名单</p>
          </li>
        </router-link>
      </ul>
    </div>
    <div class="right tt-column">
      <div class="nav">
        <div class="navx between align-center">
          <p class="nav-title">{{ $store.state.navtitle }}</p>
          <div class="tt-row nav-menu">
            <p style="margin-right: 20px" class="uid">ID：{{ uid }}</p>
            <span class="tt-row">
              <i class="iconfont icon-yonghu icon"></i>
              <p class="p1">{{ mobile }}</p>
            </span>
            <i class="bl"></i>
            <span class="tt-row" style="margin-right: 20px">
              <i class="iconfont icon-renminbicaiwuzhifu icon"></i>
              <span class="p1 tt-row"
                >余额：
                <p style="color: red">{{ balance }}</p>
                &nbsp;元</span
              >
              <p class="uid" @click="chongzhi()">充值</p>
            </span>
            <span class="tt-row">
              <i class="iconfont icon-account_pwd icon"></i>
              <p class="p1" @click="cpsdbox = true">修改密码</p>
            </span>
            <i class="bl"></i>
            <span class="tt-row" @click="sginOut()">
              <i class="iconfont icon-dianyuan icon"></i>
              <p class="p1">退出</p>
            </span>
          </div>
        </div>
      </div>
      <div class="tips tt-row">
        <el-button class="tips-btn center-xy" type="primary" plain
          >注意</el-button
        >
        <p>{{ tips }}</p>
      </div>
      <div class="main">
        <router-view></router-view>
      </div>
  <!-- 修改密码弹窗 -->
      <el-dialog
        :show-close="false"
        title="修改密码"
        :visible.sync="cpsdbox"
        width="600px"
        center
      >
        <div class="form-box center-xy tt-column">
          <div class="tt-row">
            <div class="btn" :class="{ this: cpsd == 1 }" @click="cpsd = 1">
              修改登录密码
            </div>
            <div class="btn" :class="{ this: cpsd == 2 }" @click="cpsd = 2">
              修改支付密码
            </div>
          </div>
          <div class="full" v-if="cpsd == 1">
            <div class="between align-center marginY-10">
              <div class="form-row-left">
                <p><i style="color: red">*</i>原登录密码：</p>
              </div>
              <el-input
                type="password"
                placeholder="请输入原登录密码"
                v-model="oldpsd"
                clearable
              >
              </el-input>
            </div>
            <div class="between align-center marginY-10">
              <div class="form-row-left">
                <p><i style="color: red">*</i>新登录密码：</p>
              </div>
              <el-input
                type="password"
                placeholder="请输入新登录密码"
                v-model="newpsd"
                clearable
              >
              </el-input>
            </div>
            <div class="between align-center marginY-10">
              <div class="form-row-left">
                <p><i style="color: red">*</i>确认登录密码：</p>
              </div>
              <el-input
                type="password"
                placeholder="请输入"
                v-model="enternew"
                clearable
              >
              </el-input>
            </div>
          </div>
          <div v-if="cpsd == 2">
            <div class="between align-center marginY-10">
              <div class="form-row-left">
                <p><i style="color: red">*</i>设置支付密码：</p>
              </div>
              <el-input
                type="password"
                placeholder="请输入"
                v-model="paypsd"
                clearable
              >
              </el-input>
            </div>
            <div class="between align-center marginY-10">
              <div class="form-row-left">
                <p><i style="color: red">*</i>确认支付密码：</p>
              </div>
              <el-input
                type="password"
                placeholder="请输入"
                v-model="enterpay"
                clearable
              >
              </el-input>
            </div>
          </div>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="cpsdbox = false">取 消</el-button>
          <el-button type="primary" @click="cpsdbox = false"
            >确 定</el-button
          >
        </span>
      </el-dialog>
      <!-- 充值弹窗 -->
      <el-dialog
        :visible.sync="czbox"
        width="40%"
      >
        <div>
          <h2 style="text-indent:20px;">重要提醒:</h2>
          <p style="font-size:20px;">国家开展断卡行动，转账过程如果有收到银行或者支付宝的电话，询问是否存在诈骗之类的问题，
            <span style="color:red">请回答“给朋友转的，是货款或者买东西之类，不存在风险被骗”，</span>切勿乱回答补单、刷单字眼， 
            <span style="color:red">转账信息请不要备注刷单、平台充值、补单等相关文字</span>
            否则会打款失败。望各位谨慎</p>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="czbox = false"
            >关闭</el-button
          >
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
// import { mapMutations, mapState, mapGetters } from "vuex";
export default {
  name: "Menu",
  data() {
    return {
      viewid: 1,
      tips: "平台只保留一个月的数据，请及时核对财务数据，过时删除，无法对账，有需要请备份任务和资金明细。",
      uid: 19891331,
      mobile: 17560745114,
      balance: 3.1415926,

      oldpsd: null,
      newpsd: null,
      enternew: null,
      paypsd: null,
      enterpay: null,
      cpsdbox: false,
      cpsd: 1,
      czbox: false
    };
  },
  mounted() {
    // this.checkLogin();
  },
  computed: {},
  methods: {
    // ...mapMutations(['ctitle']),
    cview(a, title) {
      this.viewid = a;
      this.$store.commit("ctitle", title);
      this.$router.push({ path: "/withdrawPay" });
    },
    checkLogin() {
      if (!this.uid) {
        this.$notify({
          message: "请先进行登陆",
        });
        this.$router.push({ path: "/login" });
      }
    },
    chongzhi() {
      this.czbox = true
      this.cview(12, "充值提现");
    },
    sginOut() {
      this.$confirm("确认退出?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "已退出登陆!",
          });
          this.$router.push({ path: "/login" });
        })
        .catch(() => {});
    },
  },
};
</script>

<style scoped>
.bl {
  width: 2px;
  height: 14px;
  background: #f2f3f6;
  margin-right: 20px;
}
a {
  text-decoration: none;
}
.icon {
  border-radius: 50%;
  color: #c6c6c6;
  font-size: 24px;
  margin: 0 10px 0 0;
}
#menu {
  width: 100%;
  height: 100vh;
  background: #f0f1f5;
}
.left {
  min-width: 200px;
  height: 100vh;
  background: #353e50;
  padding-right: 20px;
  font-size: 16px;
  overflow-y: auto;
}
.left a {
  color: #808faf;
}
.left .iconfont {
  margin: 0 10px 0 0;
}
.left ul li {
  padding: 15px 0 15px 25px;
}
.left ul .li {
  background: #3672e8;
  color: #fff;
}
.right {
  flex-grow: 1;
  height: 100vh;
  padding: 0 20px;
}
.logo {
  height: 110px;
}
.logo img {
  width: 180px;
  height: 65px;
}
.nav {
  width: 100%;
  background: #fff;
  padding: 25px 0;
}
.navx {
  height: 30px;
}
.nav .p1 {
  font-size: 16px;
  color: #868686;
  margin-right: 20px;
  font-weight: bold;
}
.nav-title {
  margin-left: 40px;
  color: #5f6737;
  font-size: 18px;
  font-weight: bold;
}
.nav-menu {
  margin: 0 20px 0 0;
}
.uid {
  font-size: 20px;
  color: #0098e1;
  font-weight: 700;
}
.tips {
  /* height: 68px; */
  width: 100%;
  display: flex;
  align-items: center;
  font-size: 18px;
  text-indent: 10px;
  padding: 20px 0;
}
.tips-btn {
  width: 56px;
  height: 28px;
  font-size: 12px;
}
.main {
  width: 100%;
  flex-grow: 1;
  overflow-x: hidden;
}
/* 修改密码弹窗 */
.form-box {
  width: 370px;
  margin: 0 auto;
}
.form-box .btn {
  padding: 10px 15px;
  background: #f0f1f5;
  border-radius: 5px;
  margin: 10px;
  transition: all 0.5s;
}
.form-box .this {
  color: #fff !important;
  background: #3672e8;
}
.form-box .btn:hover {
  color: #000000;
  background: #5ae1f7;
  transition: all 0.5s;
}
.form-row-left {
  width: 150px;
  color: #606266;
}
</style>



