<template>
  <div id="publishtask">
    <div class="tt-row">
      <h2 style="margin-right: 30px">第一步：选择任务类型</h2>
      <router-link to="pricedetail"
        ><h2 class="link">查看价格表</h2></router-link
      >
    </div>
    <div class="chosebtn">
      <div class="tt-row marginY-10">
        <div class="btn tt-row" :class="{ this: true }">
          <img src="../assets/fabu/tb.png" alt="" />淘宝
        </div>
        <div class="btn tt-row">
          <img src="../assets/fabu/jd.png" alt="" />京东
        </div>
        <div class="btn tt-row">
          <img src="../assets/fabu/pdd.png" alt="" />拼多多
        </div>
        <div class="btn tt-row">
          <img src="../assets/fabu/albb.png" alt="" />阿里巴巴
        </div>
        <div class="btn tt-row">
          <img src="../assets/fabu/dy.png" alt="" />抖音
        </div>
      </div>
      <div class="tt-row">
        <div class="btn" :class="{ this: true }">垫付任务</div>
        <div class="btn">浏览任务</div>
      </div>
    </div>
    <el-radio-group v-model="radio" class="radios tt-column">
      <el-radio value="" class="align-center marginY-20">
        <div class="tt-row">
          <img
            style="margin: 0 10px 0 40px"
            src="../assets/fabu/tb.png"
            alt=""
          />
          <p style="font-size: 15px; margin-right: 30px">手机端淘宝当日任务</p>
          <span>用户在手机淘宝APP下单</span>
        </div>
      </el-radio>
      <el-radio value="" class="align-center">
        <div class="tt-row">
          <img
            style="margin: 0 10px 0 40px"
            src="../assets/fabu/tb.png"
            alt=""
          />
          <p style="font-size: 15px; margin-right: 30px">手机端淘宝当日任务</p>
          <span>用户在手机淘宝APP下单</span>
        </div>
      </el-radio>
    </el-radio-group>
    <div class="shop">
      <h2>第二步：选择店铺</h2>
      <div class="tt-row">
        <div class="btn">
          <p>+绑定店铺</p>
        </div>
      </div>
    </div>
    <div class="backp">
      <h2>第三步：选择平台返款模式</h2>
      <div class="tt-row align-start">
        <el-radio value="" class="align-center"> </el-radio>
        <div>
          <span style="line-height: 20px">平台系统返款</span><br />
          <span class="tt-row"
            ><p>优先展示给用户</p>
            ,商家押商品本金到平台，只需在“返款管理”中确认返款金额，一键返款给用户(
            <p>36小时内</p>
            )，商家无需耗费时间，人力处理退款</span
          >
        </div>
      </div>
    </div>
      <div class="next-btn" @click="next">下一步</div>
  </div>
</template>

<script>
export default {
  name: "publishtask1",
  metaInfo: {
    title: "淘小富-发布任务",
    meta: [
      { charset: "utf-8" },
      {
        name: "viewport",
        content:
          "width=device-width,initial-scale=1,minimum-scale=1,maximum-scale=1,user-scalable=no",
      },
    ],
  },
  data() {
    return {
      radio: null,
    };
  },
  methods:{
      next(){
          this.$router.push({ path: "publishtask/publishtask2" });
      },
      prev(){
          this.$router.go(-1)
      }
  }
};
</script>

<style scoped>

.task {
  margin-top: 20px;
  width: 100%;
  padding: 20px;
  background: #fff;
}
.chosebtn,
.radios,
.shop {
  border-bottom: 1px solid #f0f1f5;
  padding: 20px 0;
}
.task h2,
.radios h2,
.shop h2,
.backp {
  padding: 20px 0;
}
.chosebtn .btn {
  height: 50px;
  background: #f0f1f5;
  border-radius: 4px;
  padding: 0 30px;
  margin-right: 30px;
}
.chosebtn .btn img {
  margin-right: 10px;
}
.chosebtn .this {
  background: #3573e8;
  color: #fff;
}
.shop .btn {
  padding: 0 30px;
  height: 50px;
  background: #f0f1f5;
}
.shop .btn p {
  color: #5f6372;
}
.backp span {
  color: #616574;
  line-height: 40px;
}
.backp p {
  color: #202a5b;
}
.next-btn {
  width: 280px;
  height: 50px;
  font-size: 14px;
  background: #3673e8;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  margin: 0 auto;
  margin-top: 30px;
  cursor: pointer;
}
</style>



