<template>
  <div id="sign">
    
    <div class="registr">
        <div class="tt-row">
            <router-link to="/login"><i class="el-icon-back back"></i></router-link>
            <p style="font-size: 24px; color: #fff; font-weight: 700">注册</p>
        </div>
        <div class="inputbox reg-box">
          <input type="text" class="dl-input" placeholder="手机号码" />
        </div>
        <div class="inputbox reg-box">
          <input type="text" class="dl-input" placeholder="QQ号" />
        </div>
        <div class="inputbox reg-box">
          <input type="password" class="dl-input" placeholder="密码" />
        </div>
        <div class="inputbox reg-box">
          <input type="password" class="dl-input" placeholder="确认密码" />
        </div>
        <div class="inputbox reg-box between align-center">
          <input type="number" class="dl-input" placeholder="邀请码" />
          <p class="forget">邀请码请输入纯数字</p>
        </div>
        <div class="inputbox reg-box between align-center">
          <input type="text" class="dl-input" placeholder="图形验证码" />

        </div>
        <div class="inputbox reg-box between align-center">
          <input type="text" class="dl-input" placeholder="手机验证码" />

        </div>
        <el-button class="dl-btn" type="primary" @click="registr()">立即注册</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "Sign",
  data() {
    return {
      
    };
  },
  methods:{
     registr(){
       this.$notify.error({
          title: '错误',
          message: '这是一条错误的提示消息'
        });
     } 
  }
};
</script>

<style scoped>

#sign{
  width: 100%;
  height: 100vh;
  background: url(../assets/login/loginbg.png);
  display: flex;
  justify-content: center;
  align-items: center;
}
.inputbox {
  width: 240px;
  /* height: 37px; */
  border-bottom: 1px solid #fff;
  color: #fff;
  font-size: 14px;
  position: relative;
}
.dl-input {
  height: 37px;
  border: none;
  background: none;
  color: #fff;
  font-size: 18px;
  padding: 7px 0 4px;
  font-weight: 200;
}
.forget {
  position: absolute;
  right: 0;
}
.dl-btn {
  background: #445bc2;
  transition: all 0.5s;
  height: 35px;
  width: 240px;
  border-radius: 18px;
  border: none;
}
.dl-btn:hover {
  background: #6bbef4;
  transition: all 0.5s;
}
.rempsd {
  color: #fff;
  font-size: 14px;
  margin: 10px 0 16px;
}
.registr{
    width: 290px;
    height: 412px;
    background: #00000066;
    border-radius: 20px;
    padding: 50px;
}
.back{
    font-size: 24px;
    color: #fff;
    font-weight: 700;
    margin-left: -30px;
    margin-right: 6px;
}
.reg-box{
    width: 290px;
}
.dl-btn{
  width: 100%;
  margin: 10px 0;
}

.dl-input:focus {
  outline: none;
  background: none;
}
/* 自定义input样式 */
input::-webkit-input-placeholder {
  /* WebKit browsers */
  color: #fff;
  font-size: 18px;
}
input:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #fff;
  font-size: 18px;
}
input::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #fff;
  font-size: 18px;
}
input:-ms-input-placeholder {
  /* Internet Explorer 10+ */
  color: #fff;
  font-size: 18px;
}
/* 隐藏input（number）上下按钮 */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
input[type="number"]{
  -moz-appearance: textfield;
}
</style>



