<template>
  <div>
    <h2>第一步：填写商品信息</h2>
    <div class="tt-row marginY-10">
      <p>任务名称<span style="color: red">(必填)</span>：</p>
      <div class="input"><el-input v-model="input"></el-input></div>
    </div>
    <div class="tt-row marginY-10">
      <p>商品名称<span style="color: red">(必填)</span>：</p>
      <div class="input"><el-input v-model="input"></el-input></div>
    </div>
    <div class="tt-row marginY-10">
      <p>商品链接<span style="color: red">(必填)</span>：</p>
      <div class="input"><el-input v-model="input"></el-input></div>
    </div>
    <div class="tt-row marginY-10 align-start">
      <p>商品图片<span style="color: red">(必填)</span>：</p>
      <div class="tt-row">
        <el-upload
          class="avatar-uploader"
          action="https://jsonplaceholder.typicode.com/posts/"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload"
        >
          <img v-if="imageUrl" :src="imageUrl" class="avatar" />
          <img v-else src="../assets/fabu/uploadimg.png" alt="" />
        </el-upload>
        <p style="color: #6d707f; margin-left: 20px; font-size: 14px">
          上传“商品主图”，确保与搜索页面展示的图片一致<br />
          上传图片不成功，请使用谷歌浏览器
        </p>
      </div>
    </div>
    <div class="tt-row marginY-10">
      <p>商品实际成交价格<span style="color: red">(必填)</span>：</p>
      <div class="input1"><el-input v-model="input"></el-input></div>
      <p>
        &nbsp;元&nbsp;<span class="p2">用户拍下时的单件宝贝付款金额，如不同等级买号看到商品价格不同，取最大值。</span>
      </p>
    </div>
    <div class="tt-row marginY-10">
      <p>每单拍<span style="color: red">(必填)</span>：</p>
      <div class="input1"><el-input v-model="input"></el-input></div>
      <p>
        &nbsp;件&nbsp;<span class="p2">每单总金额元【不含运费】</span>
      </p>
    </div>
    <div class="tt-row marginY-10">
      <p>手机搜索页面展示价格<span style="color: red">(必填)</span>：</p>
      <div class="input1"><el-input v-model="input"></el-input></div>
      <p>
        &nbsp;元&nbsp;<span class="p2">务必亲自在手机端搜索，确认价格准确</span>
      </p>
    </div>
    <div class="tt-row marginY-10">
      <p>手机搜索页面展示价格<span style="color: red">(必填)</span>：</p>
      <div class="input1"><el-input v-model="input"></el-input></div>
      <p>
        &nbsp;元&nbsp;<span class="p2">务必亲自在手机端搜索，确认价格准确</span>
      </p>
    </div>
    <div class="tt-row marginY-10">
      <p>是否收藏：</p>
      <el-radio v-model="radio" label="1">否</el-radio>
      <el-radio v-model="radio" label="2">是</el-radio>
    </div>
    <div class="tt-row marginY-10">
      <p>是否加入购物车：</p>
      <el-radio v-model="radio" label="1">否</el-radio>
      <el-radio v-model="radio" label="2">是</el-radio>
    </div>
  </div>
</template>

<script>
export default {
  name: "publishtask2",
  data() {
    return {
      imgUrl: "",
    };
  },
  mounted(){
      this.$store.commit("cstep",2)
  },
  methods: {
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
  },
};
</script>

<style scoped>
.p2 {
  font-size: 14px;
  color: #5f6372;
}
h2 {
  font-size: 22px;
  color: #333;
  padding: 10px 0 50px 0;
}
.input {
  width: 1000px;
}
.input2 {
  width: 230px;
}
.avatar-uploader {
  background: url(../assets/fabu/uploadimg.png);
  background-size: 100%;
  border-radius: 10px;
  overflow: hidden;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>



