<template>
  <div id="login">
    <div class="loginbox">
      <div class="l-logo align-center">
        <img src="../assets/login/logo.png" alt="" />
        <router-link class="" to="/sign">
          <el-button class="zc-btn" type="primary">新用户注册</el-button>
        </router-link>
      </div>
      <div class="dl tt-clumn">
        <p style="font-size: 24px; color: #fff; font-weight: 700">登陆</p>
        <div class="inputbox">
          <input type="text" class="dl-input" placeholder="用户名" />
        </div>
        <div class="inputbox between align-center">
          <input type="password" class="dl-input" placeholder="密码" />
          <p class="forget">忘记密码？</p>
        </div>
        <el-checkbox class="rempsd" v-model="checked">记住密码</el-checkbox>
        <el-button class="dl-btn" type="primary" @click="login()"
          >登录</el-button
        >
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      checked: false,
      flag: false,
    };
  },
  methods: {
    login() {
      this.$notify({
        title: "成功",
        message: "这是一条成功的提示消息",
        type: "success",
      });
      
      this.$router.push({path: '/'})
    },
  },
};
</script>

<style scoped>
#login {
  width: 100%;
  height: 100vh;
  background: url(../assets/login/loginbg.png) center no-repeat;
  background-size: 100% 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.loginbox {
  width: 725px;
  height: 300px;
  /* padding: 40px; */
  background: #00000066;
  border-radius: 20px;
  display: flex;
  align-items: center;
}
.l-logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 380px;
  height: 223px;
}
.l-logo img {
  width: 360px;
  height: 130px;
  margin: 20px;
}
.zc-btn {
  width: 164px;
  height: 35px;
  border-radius: 0;
}
.dl {
  width: 264px;
  height: 223px;
  border-left: 1px solid #fff;
  padding-left: 20px;
}
.inputbox {
  width: 240px;
  /* height: 37px; */
  border-bottom: 1px solid #fff;
  color: #fff;
  font-size: 14px;
  position: relative;
}
.dl-input {
  height: 37px;
  border: none;
  background: none;
  color: #fff;
  font-size: 18px;
  padding: 7px 0 4px;
  font-weight: 200;
}
.forget {
  position: absolute;
  right: 0;
}
.dl-btn {
  background: #445bc2;
  transition: all 0.5s;
  height: 35px;
  width: 240px;
  border-radius: 18px;
  border: none;
}
.dl-btn:hover {
  background: #6bbef4;
  transition: all 0.5s;
}
.rempsd {
  color: #fff;
  font-size: 14px;
  margin: 10px 0 16px;
}

.dl-input:focus {
  outline: none;
  background: none;
}
input::-webkit-input-placeholder {
  /* WebKit browsers */
  color: #fff;
  font-size: 18px;
}
input:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #fff;
  font-size: 18px;
}
input::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #fff;
  font-size: 18px;
}
input:-ms-input-placeholder {
  /* Internet Explorer 10+ */
  color: #fff;
  font-size: 18px;
}
</style>



